@import url(https://fonts.googleapis.com/css2?family=Jua&family=Noto+Serif+KR&display=swap);
.result_title {
  margin: 3rem 3rem 2rem;
  color: var(--font-color);
  font-family: var(--title-font);
  white-space: pre-line;
  display: block;
  font-size: 2em;
  -webkit-margin-before: 1.67em;
          margin-block-start: 1.67em;
  -webkit-margin-after: 1.67em;
          margin-block-end: 1.67em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
}

.result_explain {
  padding: 1px 6px;
  border-width: 2px;
  border-style: solid;
  border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  border-image: none;
  border-image: initial;
  width: 30em;
  height: 50em;
  background-color: rgb(233, 233, 233);
  margin-left: auto;
  margin-right: auto;
}

.result_list {
  text-align: left;
  font-family: var(--content-font);
  font-size: 1.2em;
}

:root {
  --bg-color: #fafbf2;
  --font-color: black;
  --title-font: "Jua", sans-serif;
  --content-font: "Jua", sans-serif;
}

body {
  height: auto;
  display: block;
  /* position: -webkit-sticky; */
  background-color: #fafbf2;
  background-color: var(--bg-color);
  text-align: center;
  justify-content: center;
}

.main_img {
  margin: 0;
  max-width: 550px;
  width: 100%;
  height: auto;
  font-family: "Jua", sans-serif;
  font-family: var(--title-font);
  cursor: pointer;
}
.button {
  padding: 1px 6px;
  border-width: 2px;
  border-style: outset;
  background-color: rgb(233, 233, 233);
  border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  border-image: none;
  border-image: initial;
}
.option-btn {
  font-family: "Jua", sans-serif;
  font-family: var(--content-font);
  font-weight: bolder;
  font-size: 1.2rem;
  height: 5em;
  width: 20rem;
  margin: 0.5em;
  word-break: keep-all;
  white-space: pre-line;
}
.option-rbtn {
  font-family: "Jua", sans-serif;
  font-family: var(--content-font);
  font-weight: bolder;
  font-size: 1.2rem;
  height: 11em;
  width: 20rem;
  margin: 0.5em;
  word-break: keep-all;
  white-space: pre-line;
}
.question {
  margin: 3rem 3rem 2rem;
  color: black;
  color: var(--font-color);
  font-family: "Jua", sans-serif;
  font-family: var(--title-font);
  white-space: pre-line;
  display: block;
  font-size: 2em;
  -webkit-margin-before: 1.67em;
          margin-block-start: 1.67em;
  -webkit-margin-after: 1.67em;
          margin-block-end: 1.67em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
}

